/* Modal Overlay */
/* Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000; /* Ensure it's higher than other elements */
}

/* Modal Content */
.modal-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  max-width: 100%;
  max-height: 100%;
  width: 55%;
  height: 82%;
  overflow: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  align-items: center;
  position: relative; /* For positioning the close button */
}

.modal-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #ff0000; /* Red background */
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #fff; /* White color for the X */
  width: 40px;
  height: 40px;
  border-radius: 50%; /* Make it circular */
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-close-button:hover {
  background: #cc0000; /* Darker red on hover */
}

/* Modal Image Base Style */
.modal-image {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 5px;
}

/* Brochures specific styles */
.modal-image.brochures {
  width: calc(50% - 10px);
  height: 720px;
}

/* Furnitures specific styles */
.modal-image.furnitures {
  width: calc(50% - 10px);
  height: 620px;
}

/* Menus specific styles */
.modal-image.menus {
  width: calc(50% - 10px);
  height: 720px;
}

/* Responsive adjustments if needed */
@media (max-width: 768px) {
  .modal-content {
    width: 90% !important;
    height: 90% !important;
    padding: 0px;
  }
  .modal-image {
    width: 100%;
    height: auto;
  }
}

@media (max-width: 480px) {
  .modal-content {
    width: 95% !important;
    height: 65% !important;
  }

  .modal-image {
    height: auto !important;
    width: 500px !important;
  }
}


body.no-scroll {
  overflow: hidden;
  pointer-events: none; /* Disable all interactions */
}

body.no-scroll .modal-overlay {
  pointer-events: all; /* Enable interactions only for modal */
}



